<template>
<link rel="preconnect" href="https://fonts.gstatic.com">
<link href="https://fonts.googleapis.com/css2?family=Carter+One&display=swap" rel="stylesheet">
<!--Otras letras: Wendy One, Acme, Coiny, Righteous, Lilita One-->

    <div class="selection-div">
        <h1>Main Game HERE</h1>
        <!--
            <button @click="gameOver">Finish Game</button>
            <h2>Character</h2> <h2>{{ character }}</h2>
            <h2>Level</h2> <h2>{{ level }}</h2>
            <span class="sprite" :style="{'background-image': 'url(' + require('@/assets/characters/spritesheet.png') + ')'}" ></span>
        -->
        <div class="sprite" :style="{ 'background': 'url(' + require('@/assets/characters/sprite-ex.png') + ')','background-position': `${animation[currentFrame].x * -32}px ${animation[currentFrame].y * -32}px`}"></div>
        <!-- x 14     y 18 -->
    </div>

</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { STAGES, CHARACTERS, LEVELS } from '@/consts.js';

export default {
    name: 'MainGame',
    components: {
    },
    props: [
        'levelID',
        'characterID'
    ],
    emits: ['gameIsOver'], //lo que esta emitiendo a otros componentes
    data() {
        return {
            points: 0,
            level: LEVELS[ this.$props['levelID'] ],
            character: CHARACTERS[ this.$props['characterID'] ],
            start: undefined,
            animation: [
                //MAL
                { x: 0, y: 1 }
            ],
            currentFrame: 0,
            frames: 1,
            speed: 1000
        }
    },
    computed: {
        ...mapState( ['stage'] )
    },
    methods: {
        gameOver() {
            this.$emit('gameIsOver');
        },
        steps(timestamp) {
            if (this.start === undefined) {
                this.start = timestamp
            }
            const elapsed = timestamp - this.start
            if (elapsed >= this.speed) {
                 this.start = timestamp
                    this.currentFrame++
                if (this.currentFrame >= this.frames) {
                    this.currentFrame = 0
                }
            }

            window.requestAnimationFrame(this.steps)
        }
    },
    created() {
        window.requestAnimationFrame(this.steps)
    }
}
</script>

<style scoped>
.selection-div {
    padding: 10px;
    background-color: red;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /*width: calc(100% - 20px);*/
    /*min-height: calc(100vh - 20px);*/
    background: radial-gradient(#7a1599, #500a44);
    font-family: 'Carter One', arial;
    font-size: 1rem;
    font-weight: normal;
}

h1, h2 {
    color: #ffffff;
}

.sprite {
    width: 32px;
    height: 32px;
    zoom: 10;
    background: url('http://localhost:8080/0-143-0-0.bmp');
    display: inline-block;
    align-self: center;
}
</style>